  /* !Overlay! */
  
  #overlay{
    position: fixed; 
    z-index: 3; 
    bottom: 10px;
    right: 10px;
    width: auto;
    height: auto; 
    background: rgba(0, 0, 0, 0.8); 
    color: white; 
    border-radius: 15px; 
    text-align: center; 
    padding: 5px; 
    max-width: 30%;
  }
  
  #overlayContainer{
    position: relative; 
    color: white; 
  }
  
  .previewUL{
    padding: 5px;
    margin: 0px;
  }
  
  .previewUL #overlayFrontEnd li,  .previewUL #overlayBackEnd li{
    border: 1px solid white; 
    max-width: 90%;
  }
  
  .shownOverlay{
    display: block;
  }
  
  .hiddenOverlay{
   /* display: none;  */
  }
  
  .toggleOverlayButton{
    position: absolute;
    right: 10px; 
    background: transparent;
    border: 2px solid white; 
    border-radius: 2px; 
    top: 5px;
  }
  
  .toggleOverlayButton:hover{
    cursor: pointer;
  }
  
  #toggleOverlayButtonIcon{
    color: white; 
  }
  
